<template>
  <div class="listIcon" :style="{'background-color':show?'rgb(38,40,44)':'rgb(25,27,31)'}" @click="showIcon">
      <div class="showIcon" :style="{'background-color':show?'rgb(184,37,37)':'rgb(25,27,31)'}">
      </div>
      <img :src="path"/>
      <span>{{text}}</span>
  </div>
</template>
<script>
export default {
  name: 'ListIcon',
  data () {
    return {
    }
  },
  props:{
      text:{
          type:String
      },
      icon:{
          type:String
      },
      show:{
          type:Boolean
      },
      indexIcon:{
          type:Number
      },
      Tab:{
          type:Number
      }
  },
  computed:{
      path(){
          return `./static/img/${this.icon}.png`
      }

  },
  methods:{
      showIcon(){
          this.$emit("showIcon",{index:this.indexIcon,Tab:this.Tab})
      }
  }
}
</script>
<style scoped>
.listIcon{
    width:250px;
    height: 40px;
}
.showIcon{
    display: inline-block;
    height: 100%;
    width: 3px;
    vertical-align: middle
}
img{
    width:16px;
    margin-left: 10px;
    height: 16px;
    vertical-align: middle
}
span{
    vertical-align: middle;
    margin-left: 5px;
    color:rgb(220,221,227);
    font-size: 12px
}
</style>



