const DataType={
    isArray:function(arg){
       return Object.prototype.toString.call(arg).split(']')[0].split('[object')[1].trim()=="Array";
    },
    isFunction:function(arg){
        return Object.prototype.toString.call(arg).split(']')[0].split('[object')[1].trim()=="Function";
    }
}

module.exports=DataType


