<template>
  <div id="app">
    <Top />
    <Right />
    <Left />
    <Bottom />
  </div>
</template>
<script>
import Top from './components/Top'
import Right from './components/Right'
import Left from './components/Left'
import Bottom from './components/Bottom'
export default {
  name: 'App',
  components: {
    Top,
    Right,
    Left,
    Bottom
  }
}
</script>

<style>
*{
  margin:0;
  padding:0
  }
#app{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  width: 1280px;
  height: 837px;
}
</style>



