<template>
  <div class="Right">
    <div class="swip">
      <Swip />
    </div>
  </div>
</template>
<script>
import Swip from '../components/_widget/swip';
export default {
  name: 'Right',
   components: {
    Swip
  },
  data () {
    return {
    }
  }
}
</script>
<style scoped>
.Right{
    position: relative;
    float: right;
    width: 1030px;
    height: 714px;
    background-color: rgb(22,24,28)
}
.swip{
  position:absolute;
  left: 50%;
  transform: translateX(-50%);
}

</style>


