<template>
<div class="Left scrollBar">
  <div v-for="(item,index) in List" :key="index">
    <div class="title">
      <span>{{item.title}}</span>
    </div>
    <ListIcon :Tab="index" :indexIcon="i" :icon="item1.icon" :text="item1.text" :show="(showIndex==i)&&(Tab==index)?true:false" v-for="(item1,i) in item.List" :key="i"  @showIcon="showIcon"/>
  </div>
  <div class="musicInfo">
    <img src="../../static/img/logo.png" />
    <div class="singerInfo">
      <div class="music Name">
        <span>假如爱有天意（Live）</span>
      </div>
      <div class="music Singer">
        <a href="#">李健</a>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import ListIcon from './_widget/ListIcon'
import DataType from '../js/DataType' 
export default {
  name: 'Left',
  components:{
    ListIcon
  },
  data () {
    return {
      Tab:0,
      showIndex:0,
      List:[{
        title:"推荐",
        List:[{"icon":'music',"text":'发现音乐'},{"icon":'cost',"text":'私人FM'},{"icon":'look',"text":'LOOK直播'},{"icon":'line',"text":'视频'},{"icon":'friend',"text":'朋友'}]
        },
        {
          title:"我的音乐",
          List:[{"icon":'logo',"text":'本地音乐'},{"icon":'logo',"text":'下载管理'},{"icon":'logo',"text":'我的音乐云盘'},{"icon":'logo',"text":'我的收藏'}],
        },
        {
          title:"创建的歌单",
          List:[{"icon":'logo',"text":'我喜欢的音乐'},{"icon":'logo',"text":'年度歌单'}]
        },
        {
          title:"收藏的歌单",
          List:[]
        }],
    }
  },
  mounted(){
    let url=this.HOST+"/searchMusic";
    this.$http.get(url,{
      params:{
        name:"因为爱所以爱"
      }
    }).then(res=>{
      if(DataType.isArray(res.data.result)){
        //console.log(res.data.result);
      }
    })
  },
  methods:{
    showIcon(msg){
      this.Tab=msg.Tab?msg.Tab:0;
      this.showIndex=msg.index;
    }
  },
  computed:{
  }
}
</script>
<style scoped>
.Left{
    float: left;
    height: 714px;
    width: 249px;
    border-right:rgb(35,38,44) solid 1px;
    background-color: rgb(25,27,31);
    overflow-y: auto;
    overflow-x: hidden;
}
.title{
  width: 100%;
  height: 42px;
  line-height: 42px;
}
.title>span{
  margin-left: 5px;
  font-size: 14px;
  color: rgb(93,124,109)
}
/*滚动条整体样式*/
.scrollBar::-webkit-scrollbar {
  width: 10px;     /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
/*滚动条里面小方块*/
.scrollBar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #535353;
}
/*滚动条里面轨道*/
/* .scrollBar::-webkit-scrollbar-track {
  border-radius: 10px;
}  */

.musicInfo{
  position: absolute;
  width: 248px;
  height: 70px;
  bottom:57px;
  border: rgb(35,38,44) solid 1px;
  background-color: rgb(25,27,31);
}
.musicInfo>img{
  width: 56px;
  height: 56px;
  margin-left:5px; 
  vertical-align: middle;
}
.singerInfo{
  display: inline-block;
  height: 100%;
  width: 150px;
  font-size: 14px;
  vertical-align: middle;
  color: white;
}
.singerInfo .Name{
  height: 25px;
  line-height: 25px;
}
.singerInfo .Singer{
  height: 25px;
  line-height: 25px;
}
.music{
  height: 20px;
  margin-top:10px; 
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.music a{
  text-decoration: none;
  color: white;
}

</style>


